body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bp3-navbar {
  margin-bottom: 25px;
}

.bp3-navbar button {
  margin-right: 10px;
}

.bp3-navbar-heading {
  cursor: default;
}

.loginCardContainer {
  display: flex;
  flex-direction: column;
  margin-top: 100px;
}

.loginCardContainer .bp3-card {
  display: flex;
  flex-direction: column;
  align-self: center;
}

.loginCardContainer .bp3-card .buttonContainer {
  align-self: center;
  margin-top: 40px;
  margin-bottom: 15px;
}

img.rounded {
  object-fit: cover;
  border-radius: 50%;
}
